
const ADD_POST = 'ADD-POST'
const UPDATE_ADD_POST = "UPDATE_ADD_POST"

let initialState =  {
  postData : [],
  newPostDataTitle : '', 
  newPostDataMessage : '', 
  
}

const ContentReducer = (state = initialState, action) =>{

  switch (action.type) {
    case ADD_POST: {

      let pushNew = {id:6, title: state.newPostDataTitle, message: state.newPostDataMessage}

      return {
        ...state,
        postData: [...state.postData , pushNew],
        newPostDataMessage: "",
        newPostDataTitle: ""
      }
    }
    case UPDATE_ADD_POST:{
      return{
          ...state,
          newPostDataMessage: action.message,
          newPostDataTitle: action.title, 
      }  
    }
    default:
      return state;
  } 
}
export const addPostCreator = () => ({type:ADD_POST }) 
export const updatePostCreator = (message) =>{
  return {
    type:UPDATE_ADD_POST,
    message: message,
  }
}
export const updatePostTitleCreator = (title) =>{
  return {
    type:UPDATE_ADD_POST,
    title: title,
  }
}
export default ContentReducer