import React from 'react'

const NewPost = (props) => {
// debugger
  let newPostDataMessage = props.newPostDataMessage
  let newPostDataTitle = props.newPostDataTitle

  let onAddPost = () =>{
    props.addPost()
    
  }
  let updatePostMessage = (e) => {
    let message = e.target.value
    props.updatePostMessage(message)
  }
  let updatePostTitle = (e) => {
    let title = e.target.value
    props.updatePostTitle(title)
  }

  return (
    <div className='newPosts'>
      <input className='newPosts-title' type='text'   placeholder='Title'
      
      onChange={updatePostTitle}
        value={newPostDataTitle}
      />
      <textarea   className='newPosts-input' type='text' placeholder='Write something...'
        onChange={updatePostMessage}
        value={newPostDataMessage}
      />
      <button className='newPosts-btn' onClick={onAddPost}>
        Posts    
      </button>     
    </div>
  )
}

export default NewPost

