import React from 'react'
import NewPost from './NewPost'
import {addPostCreator , updatePostCreator ,updatePostTitleCreator} from '../../../components/redux/ContentReducer'
import { connect } from 'react-redux'

let mapStateToProps = (state) =>{
  return{
    newPostDataTitle: state.contentPage.newPostDataTitle,
    newPostDataMessage: state.contentPage.newPostDataMessage
  }
}

let mapDispatchToProps = (dispatch) =>{
  return{
    addPost: () => {
      dispatch(addPostCreator())
    },
    updatePostMessage: (message) => {
      dispatch(updatePostCreator(message))
    },
    updatePostTitle: (title) => {
      dispatch(updatePostTitleCreator(title))
    }
  }
}

const NewPostContainer = connect(mapStateToProps,mapDispatchToProps)(NewPost)

export default NewPostContainer





















