import './App.css';
import React from 'react'
import './App.scss'
import NavBar from './components/navbar/NavBar';
import Content from './components/content/Content';
import Set from './components/navbar/set/Set';
import Messages from './components/messages/Messages';
import {Route, BrowserRouter} from 'react-router-dom'
import UsersContainer from './components/users/UsersContainer';
const App = (props)=> {

  return (
    <BrowserRouter>
    <div className="app-wrapper" >
      <NavBar/>
      <div className='app-wrapper__content'>
        <Route path='/content' render={()=>
          <Content
            // store={props.store}
            newPostDataMessage={props.state.contentPage.newPostDataMessage}
            postData={props.state.contentPage.postData}/>}/>
        <Route path='/messages' render={()=>
          <Messages 
            // store={props.store}
            myMessageData={props.state.messagePage.myMessageData} 
            chatData={props.state.messagePage.chatData}/>}/>
        <Route path='/users'
          render= {() => <UsersContainer/>}/>
      </div>
      <Set/>
    </div>
    </BrowserRouter>
  );
}

export default App;
