import React from 'react'
import {updateNewNyMessageBodyCreator, sendMyMessageCreator} from '../../../redux/MessageReducer'
import MessageFooter from './MessageFooter'
import { connect } from 'react-redux'

const mapStateToProps = (state) =>{
  return {
    newMyMessageBody: state.messagePage.newMyMessageBody
  }
}


const mapDispatchToProps = (dispatch) =>{
  return{
    handleUserKeyPress: () => {
      dispatch(sendMyMessageCreator())
    },
    onNewMessageChange: (body) =>{
      dispatch(updateNewNyMessageBodyCreator(body))
    }

  }
}


const FooterContainer = connect(mapStateToProps , mapDispatchToProps)(MessageFooter)

export default FooterContainer




















