import React from 'react'
import Post from './posts/Post'

const MyPosts = (props) => {
  
let postDataMap =
  props.postData.map((p) =>
    <Post title={p.title} message={p.message} likesCount={p.likesCount} />)
    
  return (
    <div className='myPosts'>
      {postDataMap}
    </div>
  )
}

export default MyPosts
