import React from 'react'
import Find from './find/Find'
import ChatElement from './chatElement/ChatElement'

const Chats = (props) => {



  let chatDataMap = props.chatData.map(chat =><ChatElement name={chat.name}  id={chat.id}/> )

  return (
    <div className='messages-con__chats'>
      <div className='container'>
        <Find/>
          {chatDataMap}
      </div>
    </div>
  )
}

export default Chats
