import React from 'react'
import FooterContainer from './messageFooter/FooterContainer'
import MyMessages from './myMessages/MyMessages'
// import YourMessages from './yourMessages/YourMessages'

const MessageHistory = (props) => {

  let messageDataMap = props.myMessageData.map((m) => <div className='messages-container__my'><MyMessages message={m.message}/></div>)

  return (
    <div className='messageHistory'>
      <div  className='messages-container'>
        {messageDataMap}
      </div>
      <FooterContainer 
        // store={props.store}
      />
    </div>
  )
}

export default MessageHistory
