
const UPDATE_NEW_MY_MESSAGE_BODY = 'UPDATE_NEW_MY_MESSAGE_BODY'
const SEND_MESSAGE = 'SEND_MESSAGE'

let initialState =  {
  myMessageData :[
    {id:1, message: "Today is normally wether, have a great day"},
    // {id:1, message: "Today is normally wether, have a great day"},
  ],
  newMyMessageBody:"",
  yourMessageData :[
    {id:1, message: "Today is normally wether, have a great day"},
  ],
  newYourMessageBody:{
  },
  chatData : [
    {id:1, name: "News1"},
    {id:2, name: "News2"},
    {id:3, name: "News3"},
    {id:4, name: "News4"},
    {id:5, name: "News5"},
    {id:6, name: "News6"},
    {id:7, name: "News7"},
    
  ]

}

const MessageReducer = (state = initialState,action) =>{

  switch (action.type) {
    case SEND_MESSAGE: {
      let body = { id:2 , message : state.newMyMessageBody}
  
      return {
        ...state,
        myMessageData: [...state.myMessageData , body]
      }
    }
    case  UPDATE_NEW_MY_MESSAGE_BODY : {
      return {
        ...state,
        newMyMessageBody: action.message,
      }
    }
    default:
    return state
  } 
}
export const sendMyMessageCreator = () =>  ({ type:SEND_MESSAGE })
export const updateNewNyMessageBodyCreator = (message) =>{
  return {
    type: UPDATE_NEW_MY_MESSAGE_BODY,
    message: message
  }
}
export default MessageReducer


